.testimonais {
     display: flex;
     gap: 1rem;
     padding: 0px 2rem;
}

.left-t {
     display: flex;
     flex: 1 1;
     gap: 2rem;
     flex-direction: column;
     text-transform: uppercase;
     color: white;
}


.left-t>:nth-child(1) {
     color: var(--orange);
     font-weight: bold;
}

.left-t>:nth-child(3),
.left-t>:nth-child(2) {
     font-weight: bold;
     font-size: 3rem;
}

.left-t>:nth-child(4) {
     text-transform: none;
     text-align: justify;
     letter-spacing: 4px;
     line-height: 40px;
}

.right-t {
     display: flex;
     position: relative;

}

.right-t>img {
     position: absolute;
     width: 17rem;
     height: 20rem;
     object-fit: cover;
     right: 8rem;
     top: 2rem;
     border-radius: 10px;
}

.testimonais>.right-t>:nth-child(1) {
     position: absolute;
     width: 17rem;
     height: 28rem;
     border: 2px solid orange;
     background-color: transparent;
     right: 9rem;
     border-radius: 10px;

     top: 0.9rem;
}

.testimonais .right-t>:nth-child(2) {
     position: absolute;
     width: 17rem;
     height: 19rem;
     top: 4rem;
     right: 7rem;
     background: var(--planCard);
     border-radius: 10px;

}

.arrows {
     display: flex;
     gap: 1rem;
     position: absolute;
     bottom: 1rem;
     left: 3rem;

}

.arrows>img {
     width: 1.5rem;
     cursor: pointer;
}

@media screen and (max-width:768px) {
     .testimonais {
          flex-direction: column;
     }


     .left-t>:nth-child(3),
     .left-t>:nth-child(2) {
          font-size: xx-large;
     }

     .right-t {
          display: flex;

          flex-direction: column;
          align-items: center;
          justify-content: center;
          gap: 2rem;

     }

     .right-t>div {
          display: none;
          position: relative;
     }

     .right-t>img {
          top: 0;
          right: 0;
          position: relative;
          align-self: center;
     }

     .right-t> :last-child {
          display: block;
          bottom: 0;
          left: 0;
     }

  

}